import axios from "axios";
import React, { Fragment, useEffect } from "react";
import { Link } from "react-router-dom";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";
import BASE_URL from "../apis/BaseURL";

const Register = (props) => {
  useEffect(() => {
    document.title = "Register";
  }, []);

  let registerData = {
    email: "",
    password: "",
  };

  const server_login = () => {
    axios
      .post(BASE_URL["global"] + "register", registerData)
      .then((response) => {
        console.log(response.data);
        if (response.data["result"] === "success") props(registerData);
        else if (response.data["result"] === "email already registered")
          console.log("email already registered");
        else console.log("Something Went Wrong...");
      })
      .catch((e) => {
        console.log(e);
      });
  };

  const handlePassword = (event) => {
    let password = event.target.value;
    registerData["password"] = password;
  };

  const handleEmail = (event) => {
    let email = event.target.value;
    registerData["email"] = email;
  };

  const handleKeyDown = (event) => {
    console.log(event.key);
    if (event.key === "Enter") {
      server_login();
    }
  };

  return (
    <Fragment>
      <h2>Register Screen</h2>
      <Form>
        <FormGroup>
          <Label for="exampleEmail">Email</Label>
          <Input
            onChange={handleEmail}
            type="email"
            name="email"
            id="exampleEmail"
            placeholder="with a placeholder"
          />
        </FormGroup>
        <FormGroup>
          <Label for="examplePassword">Password</Label>
          <Input
            onKeyDown={handleKeyDown}
            onChange={handlePassword}
            type="password"
            name="password"
            id="examplePassword"
            placeholder="password placeholder"
          />
        </FormGroup>
      </Form>
      <div className="text-center">
        <Button color="primary" onClick={server_login}>
          Sumbit
        </Button>
        <Link to="/home-money-management">
          <Button color="warning ml-2">login Screen</Button>
        </Link>
      </div>
    </Fragment>
  );
};

export default Register;
