import React, { Fragment, useEffect } from "react";
import { Link } from "react-router-dom";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";
import axios from "axios";
import BASE_URL from "../apis/BaseURL";

const Login = (props) => {
  useEffect(() => {
    document.title = "Login";
  }, []);

  let loginData = {
    email: "",
    password: "",
  };

  const server_login = () => {
    axios
      .post(BASE_URL["global"] + "login", loginData)
      .then((response) => {
        if (response.data["result"] === "success") props(loginData);
        else if (response.data["result"] === "password incorrect")
          console.log("password incorrect");
        else if (response.data["result"] === "email incorrect")
          console.log("email incorrect");
        else {
          console.log("Something went wrong...");
        }
      })
      .catch((e) => {
        console.log(e);
      });
  };

  const handlePassword = (event) => {
    let password = event.target.value;
    loginData["password"] = password;
  };

  const handleEmail = (event) => {
    let email = event.target.value;
    loginData["email"] = email;
  };

  const handleKeyDown = (event) => {
    console.log(event.key);
    if (event.key === "Enter") {
      server_login();
    }
  };
  return (
    <Fragment>
      <h2>Login Screen</h2>
      <Form>
        <FormGroup>
          <Label for="exampleEmail">Email</Label>
          <Input
            onChange={handleEmail}
            type="email"
            name="email"
            id="exampleEmail"
            placeholder="Email placeholder"
          />
        </FormGroup>
        <FormGroup>
          <Label for="examplePassword">Password</Label>
          <Input
            onKeyDown={handleKeyDown}
            onChange={handlePassword}
            type="password"
            name="password"
            id="examplePassword"
            placeholder="password placeholder"
          />
        </FormGroup>
      </Form>
      <div className="text-center">
        <Button color="primary" onClick={server_login}>
          Sumbit
        </Button>
        <Link to="/home-money-management/register">
          <Button color="warning ml-2">Register</Button>
        </Link>
      </div>
    </Fragment>
  );
};

export default Login;
